.sidebar {
    position: fixed;
    width: 100vh;
    height: 100vh;
    top: 0;
    right: 0;
    transform: translateX(100%);
}

.sidebar.open {
    transform: translateX(0);
    width: 40%;
    height: 100%;
    background-color: rgb(255, 255, 255);
    transition: 0.5s;
    border-radius: 12px 0 0 12px;
    z-index: 2;
}

.background {
    position: absolute;
    width: 100%;
    height: 100vh;
    top: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.56);
    z-index: 2;
}

.nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    padding: 10px 0 0 30px;
}

.close {
    cursor: pointer;
    background-color: transparent;
    border: none;
    display: flex;
    width: 100%;
    justify-content: flex-end;
    padding: 15px 65px 20px 0;
}

.closeNotOpen {
    display: none;
}

.close img {
    width: 40px;
    height: 40px;
}

@media screen and (max-width: 480px) {
    .sidebar.open {
        width: 100%;
        border-radius: 0;
        transition: 0.5s;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .nav {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0;
    }
}
